import { createStore } from "vuex";
import axios from "axios";

export default createStore({
  state: {
    infouser: [],
    infoproducts: [],
    itemSelect: [],
    filterItems: "",
    listResults: [
      {
        name: "",
        qty: "",
        disassemble: false,
      },
    ],
  },
  mutations: {
    SET_INFOUSER: (state, infouser) => {
      state.infouser = infouser;
    },
    SET_INFOPRODUCTS: (state, infoproducts) => {
      state.infoproducts = infoproducts;
    },
    SET_FILTER_VALUE: (state, value) => {
      state.filterItems = value;
    },
    SET_ITEMLIST: (state, value) => {
      state.listResults = value;
    },
  },
  actions: {
    GET_INFO_FROM_API({ commit }) {
      return axios
        .all([
          axios.get("http://localhost:3000/infouser"),
          axios.get("http://localhost:3000/infoproducts"),
        ])
        .then((response) => {
          commit("SET_INFOUSER", response[0].data);
          commit("SET_INFOPRODUCTS", response[1].data);
          return response;
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },
    GET_FILTER_VALUE({ commit }, value) {
      commit("SET_FILTER_VALUE", value);
    },
    GET_LIST_ITEM({ commit }, value) {
      commit("SET_ITEMLIST", value);
    },
  },
  getters: {
    INFOUSER(state) {
      return state.infouser;
    },
    INFOPRODUCTS(state) {
      return state.infoproducts;
    },
    FILTER_VALUE(state) {
      return state.filterItems;
    },
    ITEMLIST(state) {
      return state.listResults;
    },
  },
  modules: {},
});
