<template>
  <div class="hello">
    <div class="gag" @click="modal = false"></div>
    <div class="wrap">
      <img class="logo" :src="require('@/assets/cargo.svg')" alt="" />
      <h1>{{ msg }}</h1>
      <form class="info-user form-cargo">
        <div class="info-user__title title">Personal Info</div>
        <PersonalInfo
          v-for="infouser in INFOUSER"
          :key="infouser.id"
          :user_data="infouser"
        />
        <hr />
        <div class="items-list">
          <div class="title">Items List</div>
          <div class="items-list--row" v-for="(name, i) in ITEMLIST" :key="i" :data-index="i">
            <input
              class="form-control"
              v-model="state"
              @input="filterDate(state)"
              type="text"
              autocomplete="off"
              @focus="modal = true"
              ref="currentInput"
            />
            <div class="list-arr" v-if="filterItems && modal">
              <Items
                v-for="infoproducts in filteredDate"
                :key="infoproducts.id"
                :item_data="infoproducts"
                @setItem="setItem(infoproducts.name)"
              />
            </div>
            <div
              
              v-show="i == ITEMLIST.length - 1"
              class="add-more"
            >
              add more
            </div>
          </div>
        </div>
        <button class="btn">Send</button>
      </form>
    </div>
  </div>
</template>

<script>
import PersonalInfo from "@/components/PersonalInfo.vue";
import Items from "@/components/Items.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Info",
  components: {
    PersonalInfo,
    Items,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      state: "",
      filteredDate: [],
      modal: false,
    };
  },
  computed: {
    ...mapGetters(["INFOUSER", "INFOPRODUCTS", "FILTER_VALUE", "ITEMLIST"]),
  },
  methods: {
    ...mapActions(["GET_INFO_FROM_API", "GET_FILTER_VALUE", "GET_LIST_ITEM"]),
    filterDate(value) {
      this.GET_FILTER_VALUE(value);
      //let vm = this
      //console.log();
    },
    listOfItems(value) {
      this.GET_LIST_ITEM(value);
    },
    filterItems(value) {
      let vm = this;
      vm.filteredDate = [...this.INFOPRODUCTS];
      if (value) {
        vm.filteredDate = vm.filteredDate.filter((item) => {
          return item.name.toLowerCase().includes(value.toLowerCase());
        });
      } else {
        vm.filteredDate = vm.INFOPRODUCTS;
      }
    },
    setItem(state) {
      this.state = state;
      this.modal = false;
    },
    addNewRow(state) {
      let vm = this;
      //vm.itemArr = [...this.ITEMLIST];
      this.state = state;      
      console.log(state);
      vm.ITEMLIST.push({
        disassemble: false,
        name: state,
        qty: "",
      });
      this.state = "";
    },
  },
  watch: {
    FILTER_VALUE() {
      this.filterItems(this.FILTER_VALUE);
    },
  },
  mounted() {
    this.GET_INFO_FROM_API().then((response) => {
      if (response[(0, 1)].data) {
        console.log("data arrived");
      }
    });
  },
};
</script>

